module.exports={es:{title:'Hola!, Soy Diego Fuentes',role:'Desarrollo sitios web de alta calidad con una experiencia de usuario nica, siempre aprendiendo nuevas habilidades y en constante crecimiento.',aboutme:'Conoce ms sobre m!',aboutMeTitle:'Acerca de m',description:'Soy Desarrollador Web y de Apps mviles, los sitios web los desarrollo desde cero con la ms alta calidad y atencin al detalle, desarrollo apps nativas multiplataforma para iOS y Android con React Native logrando una experiencia de usuario impecable. Tambin desarrollo APIs Rest con herramientas como Node.js y Express.js ',experienceTitle:'Experiencia',experienceDesc:'En 2014 empec a estudiar en la Universidad de Guadalajara la carrera de Ingenera en Informtica, haba aprendido a programar unos meses antes de entrar a la universidad pero apartir de ah fue cuando me motiv a aprender ms del desarrollo de software y ms en especific, desarrollo web y apps. \n\n Un ao despus aplique a mi primer empleo como programador web en un agencia de marketing en el cul estuve haciendo tareas sencillas como HTML, CSS y un poco de Javascript, En 2016 recib una oferta de empleo de un agencia de software en el cul tendra el puesto como Frontend Developer, ah aprend mucho sobre Angular.js y desde hace un ao React. \n\n Actualmente me he dedicado a trabajar remotamente y como freelance, tamben sigo estudiando da a da.',skillsTitle:'Habilidades',categoryTitles:['LENGUAJES','FRAMEWORKS','HERRAMIENTAS'],titlePortfolio:'Portafolio',portfolioDescriptions:['GIT: Instructores Tcticos es un proyecto en el cul lo que yo realize fue una interfaz construida en react.js, este sitio fue realizado en mente en un futuro cargar contenido dinmicamente desde el backend. \n\n Tambin use express.js con una librera, node mailer, para poder mandar formularios de contacto.','Nano Gasa es un sencillo proyecto en el cul requera ser muy elegante y sencillo en cunto diseo y animaciones, el sitio esta construido en dos idiomas, el cliente menciono que el tener un sitio web donde los consumidores puedan encontrar su contenido presentado de forma simple y directa le ha trado un mayor inters del mercado a su producto.','Salma Spa necesitaba tener un lugar donde centralizar toda la informacin de sus servicios y tener un medio para dar soporte a sus clientes, esta web contiene ms de 14 secciones de contenido sobre servicios de spa y masaje. \n\n Lo que realic adems de la interfaz fue organizar y posicionar de la mejor manera todo su contenido del sitio web, cuidando que el cliente tenga la mejor experiencia de usuario.'],gitTitle:'Ms proyectos en GitHub',formTitle:'Tienes un proyecto en mente?',formInput:['nombre','asunto','email','mensaje','Enviar']},en:{title:"Hi!, I'm Diego Fuentes",role:'I develop extraordinary websites with a amazing user experience, always learning new skills and growing up.',aboutme:'Learn more about me!',aboutMeTitle:'About Me',description:"I'm Web and App Developer, I build website from scratch with the highest quality and extreme detail, I work with React Native to make native apps for iOS and Android achieving a great user experience, I also develop API Rest with Node.js and Express.js ",experienceTitle:'Experience',experienceDesc:'In 2014 I started studying Computer Engineering at the University of Guadalajara, I learned to program a few months before entering university but from that moment I was motivated to learn more about software development and more in specific, development web and apps.\n\n A year later I applied to my first job as a web programmer in a marketing agency where I was doing simple tasks such as HTML, CSS and a bit of Javascript.In 2016 I received an job offer from a software agency where I worked Frontend Developer, In that job I learned a lot about Angular.js and a year ago, React. \n\n Currently I work remotely and as freelancer, I also continue studying every day.',skillsTitle:'Skills',gitTitle:'More projects on GitHub',categoryTitles:['LANGUAGES','FRAMEWORKS','TOOLS'],titlePortfolio:'Portfolio',portfolioDescriptions:['GIT: Instructores Tcticos es un proyecto en el cul lo que yo realize fue una interfaz construida en react.js, este sitio fue realizado en mente en un futuro cargar contenido dinmicamente desde el backend. \n\n Tambin use express.js con una librera, node mailer, para poder mandar formularios de contacto.','Nano Gasa es un sencillo proyecto en el cul requera ser muy elegante y sencillo en cunto diseo y animaciones, el sitio esta construido en dos idiomas, el cliente menciono que el tener un sitio web donde los consumidores puedan encontrar su contenido presentado de forma simple y directa le ha trado un mayor inters del mercado a su producto.','Salma Spa necesitaba tener un lugar donde centralizar toda la informacin de sus servicios y tener un medio para dar soporte a sus clientes, esta web contiene ms de 14 secciones de contenido sobre servicios de spa y masaje. \n\n Lo que realic adems de la interfaz fue organizar y posicionar de la mejor manera todo su contenido del sitio web, cuidado que el cliente tenga la mejor experiencia de usuario.'],formTitle:"Let's Work together!",formInput:['name','subject','email','message','Send']}};

